<template>
  <div class="film" v-if="film.data">
      {{film.data[0].title}}
      <iframe class="film__video" :src="film.data[0].iframe_src" frameborder="0" allowfullscreen></iframe>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

export default {
  name: 'Home',
  data() {
    return {
      moreFilm: '',
    }
  },
  computed: {
    ...mapGetters(['film'])
  },
  methods: {
    ...mapActions(['getFilm'])
  },
  async mounted() {
    this.getFilm(this.$route.params.id)
    let r = await fetch(`https://www.omdbapi.com/?apikey=d36d7f24&i=${this.film.imdb_id}`)
    this.moreFilm = await r.json()
  },
}
</script>
<style>
    .film {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .film__video {
      width: 50vw;
      height: 28vw;
    }
</style>